<template>
  <div class="row column header" id="app">
    <div class="header-group">
    <img src="@/assets/Logo_MISA_JSC_Vie.png" width ="200px">
    <h1 class="cover-heading">
      Tm kim nh hng <i class="fa fa-search"></i>
    </h1>
  </div>

    <div class="search-viewer">
      <div class="searchForm">
        <form  @submit.prevent="submitSearch">
          <input
            type="text"
            class="search"
            v-model="searchQuery"
            placeholder="Nhp t kha tm kim"
          />
        </form>
        <div class="flex radio-group">
            <label class="radio">
              <input type="radio" id="one" :value="1" v-model="picked" />
                <span>TFS</span>
            </label>
            <label class="radio">
              <input type="radio" id="two" :value="2" v-model="picked" />
                <span>Database</span>
            </label>
        </div>
      </div>
      <button class="raised-button ink" @click="submitSearch"
        ><i class="fa fa-search"></i> Search</button>
    </div>
    <div v-if="dataRender.length > 0">
    <h1  class="title-content-result">Kt qu tm kim</h1>
    <div class="searchResult" transition="expand">
          <component
            :is="'GroupDatabase'"
            :groupData="dataRender"
          ></component>
    </div>
  </div>
  </div>
</template>

<script>
import dataSourceFake from "./dataSource";
import GroupDatabase from "@/components/GroupDatabase.vue";
// import GroupPBI from "@/components/GroupPBI.vue";
import _ from "lodash";

export default {
  name: "App",
  components: {
    GroupDatabase,
    // GroupPBI,
  },
  mounted()
  {
    
  },
  
  data() {
    return {
      isResult: false,
      searchQuery: "",
      dataSource: dataSourceFake || [],
      dataRender: [],
      test: "",
      maxSearch: 20,
      picked:2,
    };
  },
  computed: {
    dataRenderByGroup(data) {
      let result = [];
      let group = _.groupBy(data, "groupType");
      for (let key in group) {
        let elem = {
          groupKey: key,
          groupData: group[key],
        };
        result.push(elem);
      }
      return result;
    },
  },
  methods: {
    removeVietnameseAccents(str) {
    const diacriticsMap = {
      '': 'a', '': 'a', '': 'a', '': 'a', '': 'a',
      '': 'a', '': 'a', '': 'a', '': 'a', '': 'a', '': 'a',
      '': 'a', '': 'a', '': 'a', '': 'a', '': 'a', '': 'a',
      '': 'd',
      '': 'e', '': 'e', '': 'e', '': 'e', '': 'e',
      '': 'e', '': 'e', '': 'e', '': 'e', '': 'e', '': 'e',
      '': 'i', '': 'i', '': 'i', '': 'i', '': 'i',
      '': 'o', '': 'o', '': 'o', '': 'o', '': 'o',
      '': 'o', '': 'o', '': 'o', '': 'o', '': 'o', '': 'o',
      '': 'o', '': 'o', '': 'o', '': 'o', '': 'o', '': 'o',
      '': 'u', '': 'u', '': 'u', '': 'u', '': 'u',
      '': 'u', '': 'u', '': 'u', '': 'u', '': 'u', '': 'u',
      '': 'y', '': 'y', '': 'y', '': 'y', '': 'y'
    };

    return str.replace(/[^A-Za-z0-9]/g, (char) => diacriticsMap[char] || char);
  },
    expanded(data) {
      if (data) {
        data.isShow = !data.isShow;
      }
    },
    // buildGroupRender(data) {
    //   let result = [];
    //   let group = _.groupBy(data, "groupType");
    //   for (let key in group) {
    //     let elem = {
    //       groupKey: key,
    //       groupData: group[key],
    //       isShow: true,
    //     };
    //     result.push(elem);
    //   }
    //   return result;
    // },
    submitSearch() {
      this.isResult = true;
      var data = this.findSimilarArray();
      if (data) {
        this.dataRender = data;
      }
    },
    calculateSimilarityWithArray(array, words) {
      const wordsLen = words.length;
      let similarCount = 0;
      for (let i = 0; i < wordsLen; i++) {
        let word = this.removeVietnameseAccents(words[i]);
        if (array.includes(word)) {
          similarCount++;
        }
      }

      return similarCount / wordsLen;
    },
    findSimilarArray() {
      // Nhiu mng nh khc nhau
      const arrays = this.dataSource.map((item) => item.tagSearch);

      // Ly gi tr nhp vo t ngi dng v tch thnh cc t ring l
      const inputWords = this.getKeywordsFromText(this.searchQuery);

      // To mng  lu tr  tng ng ca tng mng con
      const similarities = [];

      // Duyt qua tng mng con v tnh  tng ng vi tng t ring l trong d liu nhp vo
      for (let i = 0; i < arrays.length; i++) {
        const similarity = this.calculateSimilarityWithArray(
          arrays[i],
          inputWords
        );
        similarities.push(similarity);
      }

      // Tm ch s ca mng c  tng ng cao nht
      let result = [];
      for (let i = 0; i < similarities.length; i++) {
        if (similarities[i]) {
          result.push(this.dataSource[i]);
        }
      }
      // Hin th kt qu
      return result;
    },
    getKeywordsFromText(textSearch) {
      const stopWords = new Set([
        'c', 'v', 'l', '', 'trong', '', 'vi', 'ca', 'cho', 'ny', 'cng',
        'ny', 'no', 'n', 'th', 'nh', '', 'khi', '', 'hay', 't', 'cn',
        'ti', 'anh', 'hng', 'tim', 'mun', 'kim', 'nhng'
      ]);
      const words = textSearch.toLowerCase().replace(",","").split(" ");
      const keywords = words.filter(word => !stopWords.has(word));
      return keywords;
    }
  },
};
</script>

